import React from "react"

import image1 from "../images/projects/inventory/homepage.png"
import image2 from "../images/projects/inventory/gps.png"
import image3 from "../images/projects/inventory/report.png"
import image4 from "../images/projects/inventory/create-report.png"
import image5 from "../images/projects/inventory/srf.png"

import { FaArrowCircleLeft, FaArrowCircleRight } from "react-icons/fa"
import { Fade } from "react-slideshow-image"
import "react-slideshow-image/dist/styles.css"
import SEO from "../components/seo"
import Layout from "../components/layout"

const fadeImages = [image1, image2, image3, image4, image5]

const properties = {
  prevArrow: (
    <FaArrowCircleLeft
      style={{ width: 50, height: 90, marginRight: 30, cursor: "pointer" }}
    />
  ),
  nextArrow: (
    <FaArrowCircleRight
      style={{ width: 50, height: 90, marginLeft: 30, cursor: "pointer" }}
    />
  ),
}

const atsInventory = () => {
  return (
    <Layout>
      <div className="slide-container">
        <SEO title="Projects" />
        <Fade {...properties}>
          <div className="each-fade">
            <div className="image-container">
              <img src={fadeImages[0]} alt="sc 1" />
            </div>
          </div>
          <div className="each-fade">
            <div className="image-container">
              <img src={fadeImages[1]} alt="sc 2" />
            </div>
          </div>
          <div className="each-fade">
            <div className="image-container">
              <img src={fadeImages[2]} alt="sc 3" />
            </div>
          </div>
          <div className="each-fade">
            <div className="image-container">
              <img src={fadeImages[3]} alt="sc 3" />
            </div>
          </div>
          <div className="each-fade">
            <div className="image-container">
              <img src={fadeImages[4]} alt="sc 3" />
            </div>
          </div>
        </Fade>
        <div
          style={{
            fontSize: 25,
            marginBottom: 20,
            color: "#c8d5c3",
          }}
        >
          ATS Inventory System
        </div>
        <div>
          This was my second project while working in ATS. The goal of this web
          application is for the company to have records of all items deployed
          and returned to the office. The application also has a feature for
          generating monthly inventory reports and stock release forms for
          devices and other items.
        </div>
      </div>
    </Layout>
  )
}

export default atsInventory
